body {
    width: 100vw;
    margin: 0;
    padding: 0;
}

.nav-bar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    /* padding: 0 1em 0 1em; */
    font-family: "Inria Sans", sans-serif;
    font-weight: 700;
}

.nav-bar a {
    text-decoration: none;
    color: black;
}

.right-nav-bar {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
}

.connect-btn {
    padding-right: 0px;
    font-family: "Inria Sans", sans-serif;
    background-color: #2C2C2C;
    padding: 10px;
    color: white;
    border-radius: 10px;
}

.tracking-section {
    position: relative;
    width: 100%;
    height: 100%; 
    font-family: "Inria Sans", sans-serif;
    font-weight: 700;
}

.tracking-section img {
    display: block;
    width: 100%;
    height: 400px; 
    object-fit: cover; 
}

.tracking-layout {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    color: white; 
    background: rgba(0, 0, 0, 0.5); 
}

.track-quote {
    width: 50%;
    /* padding: 1em; */
}

.track-quote h1 {
    font-size: 48px;
    margin-bottom: 0px;
}

.track-quote button {
    font-family: "Inria Sans", sans-serif;
    font-weight: 700;
    padding: 10px;
    background-color: black;
    border: 3px solid white;
    color: white;
    border-radius: 10px;
}

.trackinput-layout {
    width: 50%;
    display: flex;
    align-items: center;
    height: 100%;
    font-family: "Inria Sans", sans-serif;
    font-weight: 700;
}

.track-box {
    background-color: #2C2C2C;
    height: 60%;
    width: 70%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.benefits-section {
    padding: 1em;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
}

.benefit1-layout, .benefit2-layout {
    position: relative;
    width: 100%;
    height: 100%;
}

.benefit1-layout h4,
.benefit2-layout h4 {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}






